@font-face {
  font-family: 'icomoon';
  src:  url('assets/fonts/icomoon.eot?3s2q6m');
  src:  url('assets/fonts/icomoon.eot?3s2q6m#iefix') format('embedded-opentype'),
    url('assets/fonts/icomoon.ttf?3s2q6m') format('truetype'),
    url('assets/fonts/icomoon.woff?3s2q6m') format('woff'),
    url('assets/fonts/icomoon.svg?3s2q6m#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-menu:before {
  content: "\e902";
}
.icon-moon:before {
  content: "\e901";
}
.icon-sun:before {
  content: "\e900";
}
