// ------------ Framework ------------
*, *:before, *:after { box-sizing: border-box; }

html {
	font-size: 100%;
	height: 100%;
	overflow-y: auto;
	-webkit-text-size-adjust: 100%;
	-ms-text-size-adjust: 100%;
}

body {
	height: 100%;
	margin: 0;
	padding: 0;
}

img {
	height: auto;
	max-width: 100%;
}
